.projects-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100vw;
    padding-bottom: 142px;

    #portfolio {
        position: absolute;
        top: 0;
    }

    .more {
        color: #006DDB;
        align-self: center;
        margin-top: 70px;
        font-size: 20px;
        font-family: "Inter", sans-serif;
        font-weight: 500;
        text-decoration: none;
        transition: 0.15s all;

        &:hover {
            scale: 1.05;
        }
    }

    h1 {
        max-width: 860px;
        margin-bottom: 140px;
    }

    h3 {
        margin-bottom: 16px;
        letter-spacing: -0.035em;
    }

    .project-list {
        align-self: center;
        display: flex;
        gap: 18px;
        max-width: 1400px;
        flex-wrap: wrap;

        img {
            max-width: 690px;
            height: 365px;
            flex-wrap: wrap;
        }
    }
}

a {
    position: relative;

    ::after {
        background-color: rgba(0, 0, 0, 0.8);
        width: 100px;
        height: 26px;
        border-radius: 12px;
        color: white;
        font-family: "Inter", sans-serif;
        padding: 4px;
        left: 40%;
        opacity: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 30;
        position: absolute;
        transition: 0.2s all;
    }

    &:hover {
        ::after {
            opacity: 1;
        }
    }
}

.ecom {
    ::after {
        content: "React E-Commerce Store";
        width: 210px;
    }
}

.cv {
    ::after {
        content: "CV Creator";
    }
}

.reddit {
    ::after {
        content: "Reddit Clone";
        width: 110px;
    }
}

.mac {
    ::after {
        content: "MacOS Ventura Clone";
        width: 190px;
    }
}

.ecom,
.cv {
    ::after {
        top: -30px;
    }

    &:hover {
        ::after {
            top: -45px;
        }
    }
}

.reddit,
.mac {
    ::after {
        bottom: -30px;
    }

    &:hover {
        ::after {
            bottom: -45px;
        }
    }
}
